// -----------------------------------------------------------------------------
// Code
// -----------------------------------------------------------------------------

code, pre {
	white-space: pre;
}

pre {
	tab-size: 4;
	 code {
		background: $brand-color-mid;
		display: block;
		padding-left: $horizontal-space;
		padding-right: $horizontal-space;
		overflow: auto;
		-webkit-overflow-scrolling: touch;
	}
}

pre.code-block::before {
	content: attr(rel);
  background: $brand-color-3;
  display: block;
  color: $brand-color-2;
  text-transform: uppercase;
  font-family: $font-monospace;
	font-size: .65rem;
	font-weight: bold;
	letter-spacing: .15rem;
}

code {
	background: rgba($brand-color-1, 0.1);
}

// Code block font styles
.comment {
	color: $mid-gray;
}

pre {
  @include baseline(
		$no-text: true,
		$margin: 1,
		$padding-top: 0,
		$below: 0
	);
}

pre:before {
  @include baseline(
		$font-size: $english,
		$line-height: 1.5
	);
  // Use mixin but override unwanted parts
  margin-bottom: 0;
  padding-left: 1rem;
  padding-bottom: .05rem;
}

.code-inline {
  line-height: 1;
  font-family: $font-family-inline-code;
  font-weight: normal;
}

code:not(.code-inline) {
  @include baseline(
		$font-size: $augustin,
		$line-height: 2,
		$above: .5,
		$margin-bottom: 0
	);
	padding-bottom: font-scale(1);
}
